@charset "UTF-8";
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, button, input, textarea, hr, p, blockquote, th, td {
  margin: 0;
  padding: 0; }

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%; }

fieldset, img {
  border: 0; }

address, caption, cite, code, dfn, em, i, strong, th, var {
  font-style: normal;
  font-weight: normal; }

ol, ul, li {
  list-style: none; }

hr {
  height: 0;
  border: none; }

caption, th {
  text-align: left; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal; }

a, a:hover, a:active, a:link {
  text-decoration: none; }

/*CSS reset*/
html {
  font-size: 10px; }

html, body {
  height: 100%; }

body {
  font-size: 0.16rem;
  font-weight: normal;
  background: #1F2C4F;
  color: #fff; }

button {
  background: none;
  border: none;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  font-size: 0.16rem;
  color: #fff; }

input, button, select, textarea {
  outline: none; }

body, button, input, textarea {
  font-family: Tahoma, "Microsoft YaHei", "SimHei", sans-serif; }

a, a:hover, a:active, a:link {
  color: #fff; }

strong {
  font-weight: bold; }

table {
  width: 100%; }

input, button, select, textarea {
  box-sizing: border-box; }

* {
  box-sizing: border-box; }

*:before,
*:after {
  box-sizing: border-box; }

body {
  background: url("../images/bg.jpg") no-repeat #221C44;
  background-size: cover; }

::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 0.1rem;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px; }

::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  background: #525697; }

::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  background: #1f1f4e; }

@font-face {
  font-family: 'Impact';
  src: url("../fonts/impact.eot");
  src: url("../fonts/impact.eot?#iefix") format("embedded-opentype"), url("../fonts/impact.woff2") format("woff2"), url("../fonts/impact.woff") format("woff"), url("../fonts/impact.ttf") format("truetype"), url("../fonts/impact.svg#@{icon-font-svg-id}") format("svg"); }
